html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  color: #ffffff;
}

body {
  font-family: #000000;
  .container {
    margin: auto;
    max-width: 1280px;
    padding: 0px 45px 0px 45px;
    @media screen and (max-width: 425px) {
      margin: auto;
      max-width: 425px;
      padding: 0px 25px 0px 25px;
    }
  }
}
ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}


.title{
  font-family: $font_details;
  font-weight: normal;
  font-size: 36px;
  line-height: 1.33;
  text-transform: uppercase;
  
}